import React, { useState, useEffect } from "react";
import { Route, Routes } from "react-router-dom";
import Layout from "./Layout";
import NoMatch from "./views/public/NoMatch";
import About from "./views/public/About";
import Home from "./views/Home";
import Settings from "./views/_shared/Settings";
import { Auth } from "./utils/auth";
import Loader from "./components/Loader";
import ROUTES from "./utils/routes";
import Account from "./views/_shared/Account";
import Login from "./views/public/Login";
import Register from "./views/public/Register";
import ResetPassword from "./views/public/ResetPassword";

const AUTH_STATES = {
  waiting: "waiting",
  loggedIn: "loggedIn",
  loggedOut: "loggedOut",
};

function App() {
  const [values, setValues] = useState({
    user: null,
    authState: AUTH_STATES.waiting,
    loading: true,
  });

  useEffect(() => {
    const auth = new Auth(window);

    const user = auth.getUser();
    console.log(user);
    if (user && user.isActivated === true) {
      setValues((state) => ({
        ...state,
        authState: AUTH_STATES.loggedIn,
        loading: false,
      }));
    } else {
      setValues((state) => ({
        ...state,
        authState: AUTH_STATES.loggedOut,
        loading: false,
      }));
    }

    auth.onAuthStateChanged((user) => {
      if (user) {
        setValues((state) => ({
          ...state,
          authState: AUTH_STATES.loggedIn,
          loading: false,
        }));
      } else {
        setValues((state) => ({
          ...state,
          authState: AUTH_STATES.loggedOut,
          loading: false,
        }));
      }
    });

    return () => {};
  }, []);

  if (values.loading) {
    return <Loader />;
  }

  if (values.authState === AUTH_STATES.loggedIn) {
    return (
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<Home />} />
          <Route path={ROUTES.public.about.path} element={<About />} />
          <Route path={ROUTES.shared.account.path} element={<Account />} />
          <Route path={ROUTES.shared.settings.path} element={<Settings />} />

          <Route path="*" element={<NoMatch />} />
        </Route>
      </Routes>
    );
  } else {
    return (
      <Routes>
        <Route path={ROUTES.public.register.path} element={<Register />} />
        <Route
          path={ROUTES.public.resetPassword.path}
          element={<ResetPassword />}
        />
        <Route path="*" element={<Login />} />
      </Routes>
    );
  }
}

export default App;
