import FoodleAPI from "./api";
import { translate } from "./translater";

const LOCAL_STORAGE_USER_METADATA = "foodleData";
const LOCAL_STORAGE_SESSION_KEY = "foodleSession";
const EXPIRY_TIME = 1000 * 60 * 60 * 7; //7d
const EVENTS = {
  authStateChanged: "authStateChanged",
};

const handleError = (error, callback) => {
  console.log(error);
  if (error.response) {
    callback(error.response, null);
  } else if (error.request) {
    console.error("Request", error.request);
    callback(translate("unknown-error"), null);
  } else {
    callback(error.message, null);
  }
};

class Auth {
  constructor(window) {
    if (!window) throw Error("window object is required");

    this._context = window;

    this._api = new FoodleAPI();

    this.attachEvents();
    this._session = new Session();
    this._session.init();
  }

  attachEvents() {
    /**
     * Attach custom auth events
     */
    Object.values(EVENTS).forEach((event) => new Event(event));

    document.addEventListener(
      EVENTS.authStateChanged,
      (e) => {
        console.log(e);
      },
      false
    );
  }

  onAuthStateChanged(callback) {
    callback(this._session.getUser());
  }

  static async refresh() {
    // this._api.isLoggedIn();
  }

  login(username, password, callback) {
    this._api
      .login(username, password)
      .then(({ data }) => {
        console.log();
        const { message, error, code } = data;
        if (code) {
          error = translate(code);
        }
        callback(error, { data, message });
      })
      .catch((err) => handleError(err, callback));
  }

  static logout() {
    this.context.localStorage.removeItem(
      LOCAL_STORAGE_USER_METADATA,
      this.toString()
    );
  }

  getUser() {
    return this._session.getUser();
  }

  save() {
    this._context.localStorage.setItem(
      LOCAL_STORAGE_USER_METADATA,
      this.toString()
    );
  }

  toString() {
    return JSON.stringify({
      createdAt: this.createdAt,
      token: this.token,
    });
  }
}

class Session {
  constructor() {
    this.savedAt = null;
  }

  init() {
    const storage = this.getStoredData();

    if (
      !storage ||
      new Date(storage?.savedAt).getTime() - new Date().getTime() > EXPIRY_TIME
    ) {
      Auth.refresh();
    } else {
      this.savedAt = storage.savedAt;
    }
  }

  getStoredData() {
    let storage = window.localStorage.getItem(LOCAL_STORAGE_SESSION_KEY);

    if (storage) {
      storage = JSON.parse(storage);
    }
    return storage;
  }

  getUser() {
    const storage = this.getStoredData();
    return storage?.user;
  }

  save(window) {
    if (!window) throw Error("window object is required");
    window.localStorage.setItem(LOCAL_STORAGE_SESSION_KEY, this.toString());
  }

  toString() {
    return JSON.stringify({
      savedAt: new Date().getTime(),
      token: this.token,
    });
  }
}

export { Auth };
